@import "../../styles/common/constants.scss";

body {
  display: flex;
  flex-direction: column;
}

.landing {

  &-wrapper {
    font-family: $font-family-main, $fallback-font;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    min-height: 100%;
    width: 100%;
    flex: 1 0 auto;
  }

  &__content {
    flex: 1 0 auto;
  }

  &__img-wrapper {
    min-height: 830px;
    width: 100%;
    height: 100%;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: center;
    background-image: url("../../assets/img/jpg/landing.jpg");
  }

  &__img-header h1 {
    height: 48px;
    width: 296px;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: right;
    color: $darkShade75;
  }

  &__img-header {
    margin: 0px auto 19px auto;
    max-width: $container-width - 250px;
    width: 100%;
    padding: 0px 10px 0px 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  .landing footer {
    flex: 0 0 auto;
  }

  &__search-wrapper {
    margin: 70px auto;
    width: 100%;
    max-width: $container-width - 250px;
    padding: 0px 15px 0px 15px;
  }

  @media (max-width: $container-mobile-width) {

    &-wrapper {
      max-width: none;
    }

    .landing__search-wrapper {
      margin: 10px auto;
      max-width: 380px;
      width: 100%;
      padding: 0px;
    }

    .landing__img-wrapper h1 {
      display: none;
    }

  }
}
